
.kugou-play {
  width: 100%;
  height: 4.2143rem;
  position: fixed;
  left: 0;
  bottom: 0;
  background: rgba(0, 0, 0, .9);
  color: #fff;
  font-size: 0;
}
.kugou-play .gs-img {
  display: inline-block;
  width: 3.75rem;
  height: 3.75rem;
  border-radius: 4px;
  vertical-align: middle;
  margin-top: 0.28rem;
  margin-left: 0.2rem;
}
.kugou-play span {
  color: white;
  display: inline-block;
  width: 8rem;
  font-size: 15px;
  vertical-align: middle;
  margin-left: 0.2rem;
}
.kugou-play-img {
  display: inline-block;
  position: relative;
}
.active{
  background: red;
}
.kugou-play-img img {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.kugou-play .kugou-loading {
  -webkit-animation: loads 1.5s infinite linear;
          animation: loads 1.5s infinite linear;
}
.kugou-play .next {
  vertical-align: middle;
  margin-left: 2rem;
}
@-webkit-keyframes loads {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    transform: rotate(0deg)
}
100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg)
}
}
@keyframes loads {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    transform: rotate(0deg)
}
100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg)
}
}
.play-index-router {
  width: auto;
  height: 100%;
  padding: 0;
}
.kugou-play-index {
  margin-top: 3.2143rem;
  margin-bottom: 4.2143rem;
  width: 100%;
  height: 100%;
}
.play-index-bg {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  -webkit-filter: blur(15px);
  position: absolute;
  top: 0;
  left: 0;
}
.kugou-play-index-title {
  background: rgba(0, 0, 0, 0);
  color: white;
  width: 100%;
  height: 3rem;
  text-align: center;
  height: 3rem;
  position: relative;
  z-index: 100;
}
.kugou-play-index-title img {
  width: 20px;
  vertical-align: middle;
  position: absolute;
  left: 10px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.kugou-play-index-title span {
  font-family: 'Microsoft Yahei';
  display: inline-block;
  line-height: 3rem;
}
.play-overlay {
  background: rgba(0, 0, 0, .6);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.kugou-play-lyrics {
  min-width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 999;
  height: 69%;
  max-width: 650px;
  color: #AFABAC;
}
.kugou-play-lyrics img {
  width: 10.7143rem;
  height: 10.7143rem;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.kugou-play-lyrics .kugou-play-lyrics-context {
  width: 100%;
  position: absolute;
  top: 11.7143rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  height: 55%;
  overflow: hidden;
}
.kugou-play-lyrics .kugou-play-lyrics-context .kugou-play-lyrics-context-index {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.kugou-play-lyrics .kugou-play-lyrics-context span {
  display: block;
  width: 100%;
  text-align: center;
  padding: 10px;
}
.kugou-player {
  width: 100%;
  position: absolute;
  bottom: 0;
}
.kugou-player .kugou-player-strip {
  width: 70%;
  height: 6px;
  background-color: black;
  margin: 0 auto;
  position: relative;
}
.kugou-player .kugou-player-strip-index {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #3195FD;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  left: 0;
}
.kugou-player .time-start {
  position: absolute;
  left: 10px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.kugou-player .time-end {
  position: absolute;
  right: 10px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.lyrics-active {
  color: red;
}



